import React, {Component} from 'react'
import { NavLink} from 'react-router-dom'
import {DropdownItemLink} from './DropdownItemLink'

import PropTypes from 'prop-types'
import './NavBar.css'

export default class DropdownItems extends Component {
    render() {
        const {items, togleName} = this.props
        return (
            <div>
                <NavLink
                    className="nav-link dropdown-toggle"
                    id="navbarDropdown"
                    role='button'
                    data-toggle="dropdown"
                    to='/service'
                    aria-haspopup="true"
                    replace
                    aria-expanded="false">
                    {togleName}
                </NavLink>
                <div className='dropdown-menu' aria-labelledby="navbarDropdown">
                    {items.map((val, i) => (
                        <DropdownItemLink key={i} to={val.to}>{val.name}</DropdownItemLink>
                    ))}

                </div>
            </div>
        )
    }
}
DropdownItems.propTypes = {
    togleName: PropTypes.string.isRequired,
    items: PropTypes
        .arrayOf(PropTypes.shape({name: PropTypes.string.isRequired, to: PropTypes.string.isRequired}).isRequired,)
        .isRequired
}



