import React, {Component} from 'react'
import {connect} from 'react-redux'
import {platform} from '../../utils/platform'
import BuildCard from '../../components/Service/BuildCard';
import {getService} from '../../reducers/init/reducer'

import {CardColumns} from 'reactstrap'
import './service.css'
import UnkownPage from '../404';
export class ServicePage extends Component {
    render() {
        if (this.props.Service) {
            const {lozung, lead, text, items} = this.props.Service.data
            const isMobile = platform()
            return (
                <div className='container'>

                    <div className="jumbotron ">
                        <div className='container'>
                            <h1
                                className={isMobile
                                ? 'display-5'
                                : 'display-4'}>{lozung}</h1>
                            <p className="lead">{lead}</p>
                            <p>{text}</p>
                            <hr className="my-4"/>
                            <div className='container'>
                                <CardColumns className='d-inline-block'>{items.map((val, i) => <BuildCard key={i} cardValue={val}/>)}</CardColumns>
                            </div>
                        </div>
                    </div>
                </div>
            )
        } else {
            return <UnkownPage/>
        }
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        Service: getService(ownProps.type, state)
    }
}

export default connect(mapStateToProps)(ServicePage)


