import React, {Component} from 'react'

import PropTypes from 'prop-types'
import Mark from '../../components/Service/Mark';

export default class Marks extends Component {
    render() {
        const {marks} = this.props

        if (marks) {
            return (
                <ul className="list-group list-group-flush">
                    {marks.map(({
                        name,
                        allMarks = 5,
                        mark
                    }, i) => (<Mark name={name} allMarks={allMarks} key={i} mark={mark}/>))}

                </ul>
            )
        } else 
            return null

    }
}

Marks.propType = {
    marks: PropTypes.arrayOf(PropTypes.shape({name: PropTypes.string.isRequired, mark: PropTypes.number.isRequired, allMarks: PropTypes.number}).isRequired,)

}


