import React, {Component} from 'react'
import {
    Card,
    CardImg,
    CardText,
    CardImgOverlay,
    CardBody,
    CardTitle,
    CardSubtitle,
    Button
} from 'reactstrap'
import Stub from '../../images/stub.png'
import Marks from '../../containers/Service/Marks';

export default class BuildCard extends Component {

    constructor(props) {
        super(props)
        this.state = {
            isLeave: true
        }
    }

    onClick() {
        this.setState({
            isLeave: !this.state.isLeave
        })
    }
    onLeave() {
        this.setState({isLeave: true})
    }

    render() {
        const {body, imgSrc, alt} = this.props.cardValue

        const {
            OverlayButtonText,
            BodyTitle,
            BodySubtitle,
            BodyText,
            BodyButtonText,
            OverlaySubtitle,
            OverlayText,
            OverlayTitle,
            marks
        } = body
        return (
            <Card inverse={this.state.isLeave} className="text-center ">
                {imgSrc ? <CardImg top src={imgSrc} alt={alt? alt : ''}></CardImg> : <CardImg top src={Stub} alt=''></CardImg>  }
                   

                {this.state.isLeave 
                    ? <CardImgOverlay style={{display:'grid'}} className='align-items-center'>
                            {OverlayTitle
                                ? <CardTitle>{OverlayTitle}</CardTitle>
                                : null}
                            {OverlaySubtitle
                                ? <CardSubtitle>{OverlaySubtitle}</CardSubtitle>
                                : null}
                            {OverlayText
                                ? <CardText >{OverlayText}</CardText>
                                : null}
                            {OverlayButtonText
                                ? <Button
                                        onClick={this
                                        .onClick
                                        .bind(this)}>{OverlayButtonText}</Button>
                                : null}
                        </CardImgOverlay>
                    : <CardBody >
                        <CardImgOverlay>
                            <button
                                type="button"
                                onClick={this
                                .onLeave
                                .bind(this)}
                                className="close"
                                aria-label='Close'>
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </CardImgOverlay>
                        <Marks marks={marks}/> {BodyTitle
                            ? <CardTitle>{BodyTitle}</CardTitle>
                            : null}
                        {BodySubtitle
                            ? <CardSubtitle>{BodySubtitle}</CardSubtitle>
                            : null}
                        {BodyText
                            ? <CardText >{BodyText}</CardText>
                            : null}
                        {BodyButtonText
                            ? <Button >{BodyButtonText}</Button>
                            : null}
                    </CardBody>
}

            </Card>
        )
    }
}



