import React, { Component } from 'react'
import './Footer.css'
export default class Footer extends Component {
    render() {
        return (
            <footer className='container mt-4'>
                <div className='line'></div>
                <p className='mt-2'>Адрес: <strong>пр-д Байдукова 3, Пенза</strong> </p>
                <em className='mt-2 '>Номер для записи: <a href='tel:+78412306806' className='color-grey'>+7(8412)<strong>306-308</strong></a></em>
                <p className='mt-4'>© 2015 Центр по уходу за автомобилем «Чистая тонна»</p>
            </footer>
        )
    }
}



