import React, { Component } from 'react'

export default class UnkownPage extends Component {
    render() {
        return (
            <div className='container mt-5'>
                <h1>Здравствуй, дорогой друг, страница не найдена </h1>
                <h2>404 - not found</h2>
                <br />
                <h3>Ты перешёл на страницу, которую ещё не сотворили ^_^</h3>
                <h5 className='float-right'>Обещали скоро сделать, подожди ; )</h5>
                <br/>
            </div>
        )
    }
}



