import React, {Component} from 'react'
import {NavLink} from 'react-router-dom'

export default class ListItemLink extends Component {
    render() {
        const {
            to,
            ...rest
        } = this.props
        return (
            <li className={'nav-item'}>
                <NavLink
                    className={'nav-link'}
                    to={to}
                    activeClassName='active'
                    exact={(to === '/')
                    ? true
                    : false}
                    {...rest}/>
            </li>

        )
    }
    
}


