import React, {Component} from 'react'
import GroupOfStars from './GroupOfStarts';
import PropTypes from 'prop-types'

import './Mark.css'
export default class Mark extends Component {
    render() {
        
        const {name, mark, allMarks} = this.props
        return (
            <li className="list-group-item d-inline-flex ">
                <strong className=" fs16">{name}</strong>
                <GroupOfStars numerTrue={mark} numerAll={allMarks}/>
            </li>
        )
    }
}

Mark.propType = {
    name: PropTypes.string.isRequired,
    mark: PropTypes.number.isRequired,
    allMarks: PropTypes.number
}


