import React from 'react'
import {Route, Switch} from 'react-router'

import ServicePage from "../containers/Service/index";
import ServiceMain from '../containers/ServiceMain/index';

export default() => (
    <Switch>
        <Route exact path='/service' component={ServiceMain}/>
        <Route
            path='/service/:type'
            component={(props) => <ServicePage type={props.match.params.type}/>}/>

    </Switch>
)


