import React, {Component} from 'react'
import PropTypes from 'prop-types'

export default class But extends Component {
    render() {
        const {but, main} = this.props
        return (
            <li >
                <p>
                    <strong>{main}</strong>
                    {but}
                </p>
            </li>
        )
    }
}

But.propType = {
    but: PropTypes.string.isRequired,
    main: PropTypes.string.isRequired
}


