import React from 'react'
import { NavLink} from 'react-router-dom'
import './NavBar.css'

export const DropdownItemLink = ({
    to,
    ...rest
}) => (<NavLink
    to={to}
    className={'dropdown-item'}
    activeClassName='active'
    {...rest}/>)


