import React from 'react'
import '../components/Main/PhotoSection.css'

export default({

    ...res
}) => {
    return (
        <div
            className='d-flex align-items-end p-4 h-100  black-back'>
            <div className='text-white' {...res}></div>
        </div>

    )
}



